.stars {
  display: flex;
  align-items: center;

  &__star {
    background-image: url("../images/star-active.svg");
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
    margin-right: 4px;

    &:last-child {
      background-image: url("../images/star.svg");
    }
  }
}
